body {
  justify-content: center;
  align-items: center;
  height: 100vh;
  margin: 0;
  display: flex;
}

.stopwatch {
  border: 1vmin dotted #000;
  border-radius: 100%;
  justify-content: center;
  width: 80vmin;
  height: 80vmin;
}

.stopwatch__minute-hand {
  transform-origin: bottom;
  background-color: #0700ff;
  width: 3vmin;
  height: 20vmin;
  margin: 0 auto;
  animation: 3600s steps(60, end) infinite minute-hand;
  position: relative;
  top: 25%;
}

.stopwatch__second-hand {
  transform-origin: bottom;
  background-color: #2c8000;
  width: 1.5vmin;
  height: 38vmin;
  margin: 0 auto;
  animation: 60s linear infinite second-hand;
  position: relative;
  top: -18.5vmin;
}

.stopwatch__center-circle {
  z-index: 1;
  background-color: #f6a603;
  border-radius: 100%;
  width: 5vmin;
  height: 5vmin;
  margin: 0 auto;
  position: relative;
  top: -20.5vmin;
}

.stopwatch__minute-hand--speed-up {
  animation: 600s steps(60, end) infinite minute-hand;
}

.stopwatch__second-hand--speed-up {
  animation: 10s linear infinite second-hand;
}

@keyframes minute-hand {
  from {
    transform: rotate(0);
  }

  to {
    transform: rotate(360deg);
  }
}

@keyframes second-hand {
  from {
    transform: rotate(0);
  }

  to {
    transform: rotate(360deg);
  }
}
